// -*- scope: emm; createEmpty: true-*-
$template-thumb-width: 300px;
$template-thumb-height: 300px;
$resizable-handle-size: 10px;

.grid-template-thumb-box {
  width: $template-thumb-width;
  height: $template-thumb-height;

  @include shadow-variant(soft);

  img {
    width: auto;
    max-width: $template-thumb-width;
    height: auto;
    max-height: $template-thumb-height;
    @include center-block;
  }
}

.grid {

  .grid-toolbar-left, .grid-toolbar-right {
    position: relative;
    float: left;
    width: 30px;

    background-color: #b2d27f;
  }

  .grid-btn {
    position: absolute;
    width: 30px; height: 30px;

    z-index: 5;

    color: #69715e;
    background-color: rgb(102,166,0);

  }

  .grid-remove-btn {
    z-index: 1;
    background-color: transparent;
  }

  .grid-edit-center {
    display: block;
    text-align: center;

    .btn {
      margin-right: -5px;
      border-right: 1px solid #80b8dc;
      border-radius: 0;
    }

    .btn:first-child {
      border-radius: 2px 0 0 2px;
    }

    .btn:last-child {
      border-radius: 0 2px 2px 0;
      border-right: none;
    }
  }

  .grid-switch-btn {
    position: absolute;
    z-index: 500;

    color: #fff;
    border-radius: 0 0 2px 0;
    background-clip: padding-box;
    @include button-variant($c-white, $c-orange, $c-orange);
  }

  .highlight-border:before, .highlight-cont:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .highlight-border:before {
    box-shadow: inset 0 0 0 2px $c-orange;
  }

  .ui-resizable-handle {
    position: absolute;
    font-family: 'FontAwesome';

    width: $resizable-handle-size;
    height: $resizable-handle-size;

    border-radius: 2px;
    background-color: $c-blue-27;

    &.ui-resizable-nw {
      top: -$resizable-handle-size / 2;
      left: -$resizable-handle-size / 2;

      cursor: nwse-resize;
    }

    &.ui-resizable-ne {
      top: -$resizable-handle-size / 2;
      right: -$resizable-handle-size / 2;

      cursor: nesw-resize;
    }

    &.ui-resizable-se {
      bottom: -$resizable-handle-size / 2;
      right: -$resizable-handle-size / 2;

      cursor: nwse-resize;
    }

    &.ui-resizable-sw {
      bottom: -$resizable-handle-size / 2;
      left: -$resizable-handle-size / 2;

      cursor: nesw-resize;
    }

    &.ui-resizable-w {
      height: calc(100% - #{$resizable-handle-size});
      top: $resizable-handle-size / 2;
      left: -$resizable-handle-size / 2;

      cursor: ew-resize;
      background-color: transparent;
    }

    &.ui-resizable-e {
      height: calc(100% - #{$resizable-handle-size});
      top: $resizable-handle-size / 2;
      right: -$resizable-handle-size / 2;

      cursor: ew-resize;
      background-color: transparent;
    }

    &.ui-resizable-n {
      width: calc(100% - #{$resizable-handle-size});
      top: -$resizable-handle-size / 2;
      left: $resizable-handle-size / 2;

      cursor: ns-resize;
      background-color: transparent;
    }

    &.ui-resizable-s {
      width: calc(100% - #{$resizable-handle-size});
      bottom: -$resizable-handle-size / 2;
      left: $resizable-handle-size / 2;

      cursor: ns-resize;
      background-color: transparent;
    }

  }

  .container-hover-accept, .container-hover-accept-alt {
    border: 2px dashed #0071b9;
    background-image: repeating-linear-gradient(-45deg, transparent, transparent 5px, rgba(174,199,209,0.5) 5px, rgba(174,199,209,0.5) 10px);

    .grid-switch-btn {
      display: none;
    }
  }

  // quickfix refactor me
  #gt-wrapper {
    position: relative;

    .tile-header {
      padding: 0;
    }

    .tile-content, &.tile-content {
      & > .row {
        $classes: xs sm md lg;

        @each $cls in $classes {
          $i: 1;
          $not-first: "& > .col-#{$cls}-#{$i}:not(:first-child)";
          $not-last: "& > .col-#{$cls}-#{$i}:not(:last-child)";

          @for $i from (1 + 1) through $grid-columns {
            $not-first: "#{$not-first}, & > .col-#{$cls}-#{$i}:not(:first-child)";
            $not-last: "#{$not-last}, & > .col-#{$cls}-#{$i}:not(:last-child)";
          }

          #{$not-first} {
            border-left: 1px solid #cccdcd;
            padding-left: 0;
          }

          #{$not-last} {
            padding-right: 0;
          }
        }
      }
    }
  }

  #buildingBlocks {
    margin-left: -7px;
  }

  #content_wrapper {
    overflow: hidden;
  }

  #div-panel {
    height: 0;
    width: 0;
    overflow: hidden;
    visibility: hidden;
  }

  #client-wrapper {
    position: relative;
    margin: 0 auto;
  }

  #template-wrapper {
    position: relative;
    overflow: hidden;
    @include clearfix();
  }

  #template {
    margin: 0 auto;
  }

  #sidebar-gutters {
    position: absolute;
    height: 100%;
    width: 30px;
    top: 0;
    left: 0;
    background-color: #b2d27f;
  }

  #sidebar-actions {
    position: absolute;
    height: 100%;
    width: 30px;
    top: 0;
    right: 0;
    background-color: #b2d27f;
  }

  #editor-progress {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: white;
  }

  #editor-progress > span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%);
    padding: 5px 10px;
    border-radius: 20px;
    color: white;
    background-color: black;
    box-shadow: 0 0 15px 3px rgba(0,0,0,0.5);
  }

  .container-row {
    overflow: auto;
    margin-top: 0;
  }


  @keyframes elem-entrance {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(0.9);
    }
    100% {
      transform: scale(1);
    }
  }

  .container-row-animated {
    animation: elem-entrance 200ms ease-in-out forwards;
  }

  .container {
    float: left;
    height: 80px;
    padding: 0;
    background-color: #CCF1FF;
    margin: 1px 0;
  }

  .div-sandbox {
    display: block;
    width: 100%;
    height: 100%;
    border: 0;
    pointer-events: none;
  }

  .div-sandbox-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }

  .highlight-cont {
    .div-sandbox-overlay {
      opacity: 0.3;
      background-color: yellow;
    }
  }

  #grid-manipulation-panel {
    padding: 10px;
    text-align: center;
  }

  #save-button {
    display: none;
  }

}

#navbar-save-button, #navbar-disabled-save-button {
  display: none;
}

body.dark-theme .grid {

  .grid-toolbar-left,
  .grid-toolbar-right {
    background-color: $c-dt-grid-gutters-button;
  }

  .grid-btn {
    color: $c-dt-grid-gutters-button-text;
    background-color: $c-dt-grid-gutters-button;

    &:hover,
    &:focus {
      background-color: darken($c-dt-grid-gutters-button, 10%);
    }

    &:active {
      background-color: darken($c-dt-grid-gutters-button, 15%);
    }
  }

  #sidebar-gutters,
  #sidebar-actions {
    background-color: $c-dt-grid-gutters;
  }

  .container {
    background-color: $c-dt-grid-container-background;
  }

  .grid-switch-btn {
     @include button-variant($c-white, $c-orange, $c-orange);
  }

  #gt-wrapper {
    .tile-content,
    &.tile-content {
      & > .row {
        $classes: xs sm md lg;

        @each $cls in $classes {
          $i: 1;
          $not-first: "& > .col-#{$cls}-#{$i}:not(:first-child)";

          @for $i from (1 + 1) through $grid-columns {
            $not-first: "#{$not-first}, & > .col-#{$cls}-#{$i}:not(:first-child)";
          }

          #{$not-first} {
            border-color: $c-dt-tile-border;
          }
        }
      }
    }
  }
}

.grid-building-block {
  padding: 10px 20px 20px 20px;
  cursor: move;

  @include clearfix();
  @include user-select(none);

  span {
    font-size: 13px;
    font-weight: 700;
    word-break: break-all;
    margin-bottom: 5px;
  }

  &.ui-draggable-dragging {
    background-color: rgba(245, 249, 252, 0.5);
    @include shadow-variant(soft);
  }

}

// draft
.ui-modal-loading:after {
  content: '';
  position: absolute;
  top: 0; bottom: 0;
  left: 0; right: 0;

  background-image: url('./core/images/facelift/loading.gif');
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #fff;
  z-index: 100;
}

.div-container {
  color: white;
  word-wrap: break-word;
  position: absolute;
  min-height: 10px;

  img  {
    width: auto;
    max-width: none;
    height: auto;
    display: block;
  }
}

.div-container:not(.ui-resizable-autohide) .div-sandbox-overlay {
  border: 1px dashed $c-blue-22;
  &.deleted {
    border: 2px solid $c-red-02;
    .delete-badge {
      font-size: 11px;
      padding: 2px;
      display: inline-block;
      background-color: $c-red-02;
    }
  }
}

.div-container::after {
  content: "";
  background: url('./core/images/checkerboard.png');
  background-position: center center;
  background-repeat: repeat;
  background-size: 20px;
  opacity: 0.1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.div-container-controls {
  display: none;
  position: absolute;
  top: 50%;
  width: 100%;
  padding: 5px;
  @include translate(0, -50%);
}

.div-container-targetgroup {
  display: block;

  margin-top: 5px;
  text-align: center;

  span {
    display: inline-block;
    margin-left: 4px;

    font-size: 11px;
    padding: 3px 10px;
    border-radius: 25px;
    max-width: 100%;
    color: white;
    background-color: rgba(0,0,0,0.5);
  }
}

.popover-content {
  .div-container-targetgroup {
    margin: 0 0 10px 0;
  }

  .grid-edit-center {
    display: block;
    text-align: center;
  }
}

#dialog-modal {
  overflow: hidden;
  padding: 0;

  .ui-modal-content {
    height: 425px;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 15px;
  }

  .ui-modal-foot {
    @include clearfix();

    border-top: 1px solid #e2e3e3;
    padding: 15px;
  }

}

#available-div-containers-dialog {
  overflow: hidden;
  padding: 0;

  .ui-modal-content {
    height: 425px;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0;
  }

  .ui-modal-foot {
    @include clearfix();

    border-top: 1px solid #e2e3e3;
    padding: 15px;
  }

}

.mediapool-modal-content {
  overflow-y: scroll;
  max-height: 70vh;
}

#css-upload-modal {
  overflow: hidden;
  padding: 0;

  .ui-modal-content {
    height: 130px;
    padding: 15px;
  }

  .ui-modal-foot {
    @include clearfix();

    border-top: 1px solid #e2e3e3;
    padding: 15px;
  }

}

.grid-edit-modal {
  overflow-y: scroll;
  max-height:70vh;
}

.grid-editor-header {
  padding-left: 15px;
}

#grid-tile-content {
  position: relative;
  display: flex;
  flex-direction: row;
}

.grid-pane {
  position: relative;
  float: left;
  height: 100%;
  background-color: $c-white;

  .grid-pane-header {
    border-bottom: 1px solid $c-gray-20;
  }

  .grid-pane-body {
    // Minus tile-header height.
    height: calc(100% - 50px);
    overflow: auto;
  }
}

.grid-template-pane {
  flex-grow: 1;
  overflow-x: auto;
}

.grid-tools-pane {
  flex-shrink: 0;
  border-left: 1px solid $c-gray-20;
}

.grid-tools-handle {
  display: none;
  height: 100%;
  width: 15px;
  align-items: center;
  justify-content: center;
  color: $c-white;
  background-color: $c-blue-20;
}

.grid-tools-content {
  height: 100%;
  background-color: $c-white;
}

@media (max-width: $screen-sm-max) {
  .grid-tools-handle {
    display: flex;
  }

  .grid-tools-content {
    display: none;
  }

  .grid-tools-pane {
    position: static;
    z-index: 100;

    &:hover {
      width: auto;

      .grid-tools-content {
        position: absolute;
        top: 0;
        right: 0;
        display: block;

        border-left: 1px solid $c-gray-20;
        @include box-shadow(7px 0px 8px 8px rgba(0, 23, 39, 0.6));
      }
    }
  }
}
