// -*- scope: emm; createEmpty: true -*-
$building-block-thumb-width: 150px;
$building-block-thumb-height: 100px;
$building-block-thumb-margin: 25px;
$building-block-thumb-title-width: 200px;

img.building-block-thumb {
  width: auto;
  max-width: $building-block-thumb-width;
  height: $building-block-thumb-height;

  @include shadow-variant(soft);
}

.building-block-thumb-box {
  width: $building-block-thumb-width;
  height: $building-block-thumb-height;
  margin: 10px auto 0 auto;

  @include shadow-variant(soft);

  img {
    width: auto;
    max-width: $building-block-thumb-width;
    height: auto;
    max-height: $building-block-thumb-height;
    @include center-block;
  }
}

.building-block-thumb-title {
  max-width: $building-block-thumb-title-width;
  margin: auto;
  overflow: hidden;
  text-overflow: ellipsis;
}

body.dark-theme .building-block-thumb-box {
  background-color: lighten($c-dt-tile-header, 25%);
  @include shadow-variant(hardest);
}

.table {
  th.building-block-thumb-column {
    width: $building-block-thumb-width + $building-block-thumb-margin * 2;
  }

  td.building-block-thumb-column {
    height: $building-block-thumb-height + $building-block-thumb-margin * 2;
  }

  > tbody > tr:not(.error-row) {
    > td.building-block-thumb-column {
      background-color: $c-blue-01;
    }
  }

}

body.dark-theme .table > tbody > tr:not(.error-row) > td.building-block-thumb-column {
  background-color: $c-dt-table-background;
}

.available-building-blocks-dialog-table {
  position: relative;
  max-height: 70vh;
  overflow-y: auto;
  overflow-x: visible;
}

.flexbox {
  display: flex;
  flex-wrap: wrap;

  display: -webkit-flex;
  -webkit-flex-wrap: wrap;
}
