// -*- scope: emm; createEmpty: true -*-
.l-messenger-preview {
  padding: 2px 2px 23px 2px;
  min-height: 356px;  // Mobile (portrait)
  min-width: 322px;
  max-width: 322px;
  overflow-x: hidden;
  border-radius: 7px !important;
  background-color: $c-gray-11 !important;

  @include box-shadow(0 0px 10px 1px rgba(0,0,0,0.6) !important);

  &.preview-image-and-text, &.preview-text-only {
    padding-bottom: 13px;
  }

  &.preview-image-only {
    padding-bottom: 2px;

    .preview-time {
      padding: 2px;
      border-radius: 4px;
      background-color: black;
    }
  }

  .preview-text {
    font-size: 13px;
    border-radius: 7px;
    border: none;
    background-color: transparent;
    @include box-shadow(none !important);
  }

  .preview-image {
    display: block;
    width: auto;
    height: auto;
    margin: 0 auto;
    border-radius: 7px;
  }

  .preview-attachment {
    padding: 13px 20px;
    border-radius: 7px;
    border: solid 1px #deb887;
    background-color: lightgray;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .preview-attachment-icon {
    color: gray;
  }

  .preview-meta {
    position: absolute;
    bottom: 5px;
    left: 7px;

    font-weight: bold;
    font-size: 12px;
    line-height: 12px;
    color: $c-gray-44;
  }

  .preview-time {
    position: absolute;
    bottom: 5px;
    right: 7px;

    font-weight: bold;
    font-size: 12px;
    line-height: 12px;
    color: $c-gray-44;
  }

  .emojionearea-editor {
    padding: 7px;
    min-height: 1em;
    max-height: none;
    word-break: break-word;
  }

  .emojionearea.emojionearea-disable:before {
    opacity: 0;
  }
}