// -*- scope: openemm -*-
.pie-chart {
  padding: 10px 20px;
}

.pie-chart-header {
  font-size: $fs-large + 2px;
  line-height: 1;
  color: $c-gray-55;
  font-weight: $fw-bold;
}

body.dark-theme .pie-chart-header {
  color: $c-chart-text;
}

.chart-controls {
  padding: 10px;

  .form-group {
    margin-bottom: 0;
  }
}

.c3 svg {
  font: 10px sans-serif;
}

body.dark-theme .c3 svg .tick{
  fill: $c-chart-text;
}

.c3 path, .c3 line {
  fill: none;
  stroke: #000;
}

body.dark-theme .c3 path, .c3 line {
  stroke: $c-chart-text;
}

.c3 text {
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.c3-legend-item-tile,
.c3-xgrid-focus,
.c3-ygrid,
.c3-event-rect,
.c3-bars path {
  shape-rendering: crispEdges;
}

.c3-chart-arc path {
  stroke: #fff;

}
.c3-chart-arc text {
  fill: #fff;
  font-size: 13px;
}

/*-- Axis --*/

.c3-axis-x .tick {
}
.c3-axis-x-label {
}

.c3-axis-y .tick {
}
.c3-axis-y-label {
}

.c3-axis-y2 .tick {
}
.c3-axis-y2-label {
}

/*-- Grid --*/

.c3-grid line {
  stroke: #aaa;
}
.c3-grid text {
  fill: #aaa;
}
.c3-xgrid, .c3-ygrid {
  stroke-dasharray: 3 3;
}
.c3-xgrid-focus {
}

/*-- Text on Chart --*/

.c3-text {
  font-size: 12px;
  font-weight: $fw-bold;
}

.c3-text.c3-empty {
  fill: #808080;
  font-size: 2em;
}

.c3-text.c3-empty {
  fill: $c-chart-text;
}

/*-- Line --*/

.c3-line {
  stroke-width: 1px;
}
/*-- Point --*/

.c3-circle._expanded_ {
  stroke-width: 1px;
  stroke: white;
}
.c3-selected-circle {
  fill: white;
  stroke-width: 2px;
}

/*-- Bar --*/

.c3-bar {
  stroke-width: 0;
}
.c3-bar._expanded_ {
  fill-opacity: 0.75;
}

/*-- Arc --*/

.c3-chart-arcs-title {
  dominant-baseline: middle;
  font-size: 1.3em;
}

body.dark-theme text.c3-chart-arcs-title {
fill: $c-chart-text;
}

/*-- Focus --*/

.c3-target.c3-focused {
  opacity: 1;
}
.c3-target.c3-focused path.c3-line, .c3-target.c3-focused path.c3-step {
  stroke-width: 2px;
}
.c3-target.c3-defocused {
  opacity: 0.3 !important;
}


/*-- Region --*/

.c3-region {
  fill: steelblue;
  fill-opacity: .1;
}

/*-- Brush --*/

.c3-brush .extent {
  fill-opacity: .1;
}

/*-- Select - Drag --*/

.c3-dragarea {
}

/*-- Legend --*/

.c3-legend-item {
  font-size: 12px;
}

body.dark-theme .c3-legend-item text {
  fill: $c-chart-text;
}

.c3-legend-item-hidden {
  opacity: 0.15;
}

.c3-legend-background {
  opacity: 0.75;
  fill: white;
  stroke: lightgray;
  stroke-width: 1
}

/*-- Tooltip --*/

.c3-tooltip-container {
  z-index: 10;
}
.c3-tooltip {
  border-collapse:collapse;
  border-spacing:0;
  background-color:#fff;
  empty-cells:show;
  -webkit-box-shadow: 7px 7px 12px -9px rgb(119,119,119);
  -moz-box-shadow: 7px 7px 12px -9px rgb(119,119,119);
  box-shadow: 7px 7px 12px -9px rgb(119,119,119);
  opacity: 0.9;
}

body.dark-theme .c3-tooltip {
  color: $c-chart-tooltip-text;
}

.c3-tooltip tr {
  border:1px solid #CCC;
}
.c3-tooltip th {
  background-color: #aaa;
  font-size:14px;
  padding:2px 5px;
  text-align:left;
  color:#FFF;
}
.c3-tooltip td {
  font-size:13px;
  padding: 3px 6px;
  background-color:#fff;
  border-left:1px dotted #999;
}
.c3-tooltip td > span {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 6px;
}
.c3-tooltip td.value{
  text-align: right;
}

.c3-area {
  stroke-width: 0;
  opacity: 0.2;
}

.c3-chart-arcs .c3-chart-arcs-background {
  fill: #e0e0e0;
  stroke: none;
}
.c3-chart-arcs .c3-chart-arcs-gauge-unit {
  fill: #000;
  font-size: 16px;
}

body.dark-theme .c3-chart-arcs .c3-chart-arcs-gauge-unit {
  fill: $c-chart-text;
}
.c3-chart-arcs .c3-chart-arcs-gauge-max {
  fill: #777;
}

body.dark-theme .c3-chart-arcs .c3-chart-arcs-gauge-max {
  fill: $c-chart-text;
}
.c3-chart-arcs .c3-chart-arcs-gauge-min {
  fill: #777;
}

body.dark-theme .c3-chart-arcs .c3-chart-arcs-gauge-min {
  fill: $c-chart-text;
}

.c3-chart-arc .c3-gauge-value {
  fill: #000;
  /*  font-size: 28px !important;*/
}

body.dark-theme .c3-chart-arc .c3-gauge-value {
  fill: $c-chart-text;
}
